@font-face {
    font-family: "SF-Compact-Rounded";
    src: url(../Font/FontSFCompact/SF-Compact-Rounded-Regular.otf);
}

@font-face {
    font-family: "SF-Compact-Rounded-Medium";
    src: url(../Font/FontSFCompact/SF-Compact-Rounded-Medium.otf);
}

@font-face {
    font-family: "SF-Compact-Rounded-Semibold";
    src: url(../Font/FontSFCompact/SF-Compact-Rounded-Semibold.otf);
}

@font-face {
    font-family: "Inter-Regular";
    src: url(../Font/FontInter/Inter-Regular.ttf);
}

@font-face {
    font-family: "Inter-Medium";
    src: url(../Font/FontInter/Inter-Medium.ttf);
}

@font-face {
    font-family: "Inter-SemiBold";
    src: url(../Font/FontInter/Inter-SemiBold.ttf);
}

@font-face {
    font-family: "Inter-Bold";
    src: url(../Font/FontInter/Inter-Bold.ttf);
}

$fontFamily-Inter-Regular: "Inter-Regular", sans-serif;
$fontFamily-Inter-Medium: "Inter-Medium", sans-serif;
$fontFamily-Inter-SemiBold: "Inter-SemiBold", sans-serif;
$fontFamily-Inter-Bold: "Inter-Bold", sans-serif;

body {
    background-color: lightgrey;
    display: flex;
    justify-content: center;
}

.pickupSelection-layout {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 480px;
    width: 100vw;
    height: 100vh;
    padding: 0px 0px 20px 0px;
    background-color: #ffffff;
}

.pickupSelection-topSide {
    display: flex;
    flex-direction: column;
}

.pickupSelection-header {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid lightgrey;
    padding: 14px 14px 14px 14px;
}

.backArrow-icon {
    width: 23px;
    height: 23px;
}

.pickupSelection-title {
    font-family: $fontFamily-Inter-Bold;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    color: #111111;
    margin-left: 16px;
}

.pickup-detailContent {
    display: flex;
    height: 100%;
    padding: 0px 14px 0px 14px;
}

.pickup-radioSection {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    width: 100%;
}

.pickup-radioSection input[type="radio"] {
    display: none;
}

.pickup-radioSection label {
    display: flex;
    justify-content: space-between;
    border: 0.5px solid #aaaaaa;
    box-sizing: border-box;
    width: 100%;
    align-items: center;
    border-radius: 8px;
    height: 100%;
    padding: 12px 12px 11px 12px;
}

.pickup-radioSection input[type="radio"]:checked + label {
    border-color: #4bb7ac;
    background-color: #E6FBF9;
}

.pickup-radioSection input[type="radio"]:checked + label .pickup-radioTitle {
    color: #4bb7ac;
}

.pickup-radioSide {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.pickupradio-image {
    width: 23px;
    height: 23px;
}

.pickup-radioTitle {
    font-family: $fontFamily-Inter-SemiBold;
    font-style: normal;
    font-weight: 100;
    color: #111111;
    font-size: 18px;
    margin-left: 10px;
}

.pickupSelection-selectButton, .addressInput-selectButton {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    height: 45px;
    border-radius: 8px;
    margin-left: 14px;
    margin-right: 14px;

    font-family: $fontFamily-Inter-Bold;
    font-style: normal;
    font-weight: normal;
    text-align: center;
    font-size: 18px;
    color: white;
}

.deliverySelection-layout {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 14px 14px 0px 14px;
    margin-left: 14px;
    margin-right: 14px;
    border: 1px solid #4bb7ac;
    border-top: none;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.deliverySelection-input {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;
    border: 1px solid gray;
    border-radius: 8px;
    padding: 7px 7px 7px 12px;
    margin-bottom: 14px;
    height: 52px;
}

.deliverySelection-input-leftSide {
    display: flex;
    position: inherit;
    align-items: center;
}

.address-location-icon {
    height: 23px;
    width: 23px;
}

.deliverySelection-addressLayout {
    margin-left: 14px;
}

.deliverySelection-addressTitle {
    font-family: $fontFamily-Inter-SemiBold;
    font-style: normal;
    font-weight: 500;
    color: #111111;
    font-size: 12px;
}

.deliverySelection-addressInputted {
    font-family: $fontFamily-Inter-Regular;
    font-style: normal;
    font-weight: 400;
    color: gray;
    font-size: 12px;

    text-overflow: ellipsis;
    overflow: hidden;
    display: flex;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.deliverySelection-blackNotes {
    font-family: $fontFamily-Inter-Medium;
    font-style: normal;
    font-weight: 500;
    color: #111111;
    font-size: 12px;
    text-transform: capitalize;
}

.deliverySelection-grayNotes {
    font-family: $fontFamily-Inter-Medium;
    font-style: normal;
    font-weight: 500;
    color: #aaaaaa;
    font-size: 12px;
    text-transform: capitalize;
}

.deliverySelection-greenNotes {
    font-family: $fontFamily-Inter-Bold;
    font-style: normal;
    font-weight: normal;
    color: #4bb7ac;
    font-size: 12px;
}

.address-open-icon {
    height: 23px;
    width: 23px;
}

.deliverySelection-shipperName, .deliverySelection-shipperPrice {
    display: flex;
    flex-direction: column;
    margin-bottom: 14px;
}

.deliverySelection-shipperName-title, .deliverySelection-shipperPrice-title {
    font-family: $fontFamily-Inter-Bold;
    font-style: normal;
    font-weight: normal;
    color: #111111;
    font-size: 14px;
}

.deliverySelection-shipperPrice-layout {
    display: flex;
}

.deliverySelection-shipperPrice-currency {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 40px;
    background-color: #4bb7ac;
    border: 1px solid #4bb7ac;
    border-radius: 8px 0px 0px 8px;

    font-family: $fontFamily-Inter-Regular;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    color: #ffffff;
}

.deliverySelection-shipperName-inputArea {
    width: 100%;
    padding: 7px 7px 7px 12px;
    border: 1px solid gray;
    border-radius: 8px;
    outline: none;
}

.deliverySelection-shipperPrice-inputArea {
    padding: 7px 7px 7px 12px;
    border: 1px solid gray;
    border-radius: 0px 8px 8px 0px;
    border-left: none;
    width: 100%;
    outline: none;
}

// Address Input View
.addressInput-fullAddress, .addressInput-shipperNotes {
    margin: 14px 14px 0px 14px;
}

.addressInput-fullAddress-title, .addressInput-shipperNotes-title {
    font-family: $fontFamily-Inter-Medium;
    font-style: normal;
    font-weight: 500;
    color: #111111;
    font-size: 14px;
}

.addressInput-fullAddress-inputArea, .addressInput-shipperNotes-inputArea {
    width: 100%;
    height: 80px;
    // font-family: "SF-Compact-Rounded", sans-serif;
    font-family: inherit;
    font-style: normal;
    font-weight: normal;
    text-transform: none;
    font-size: 15px;
    color: #111111;
    background-color: transparent;
    padding-left: 14px;
    padding-top: 14px;
    border-color: #aaaaaa;
    border-radius: 8px;
    outline: none;
}

// Payment Method View
