}

.payment-radioSection input[type="radio"]:checked + label .payment-radioTitle {
    color: #4bb7ac;
    font-weight: bolder;
}

.payment-radioSide {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.paymentradio-image {
    width: 23px;
    height: 23px;
}

.payment-radioTitle {
